import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  CardFooter,
  Row,
  // Fade,
} from 'reactstrap';

import filesize from 'filesize';
import { uniqueId } from 'lodash';

import FileList from '../../components/DragDrop/FileList';
import Upload from '../../components/DragDrop/Upload';
import { ANUNCIO_DATA } from '../../constants/app';
import api from '../../services/api';

function AnuncioVideoPhotos() {
  const navigate = useNavigate();
  const { id: animalId } = useParams();
  const [showButton, setShowButton] = useState(false);
  const [uploadedFiles, setUploadedFiles] = useState([]);
  const [uploadedFileVideo, setUploadedFileVideo] = useState();

  const updateFile = (fileId, data, isVideo) => {
    if (isVideo) {
      setUploadedFileVideo({ ...uploadedFileVideo, ...data });
      return;
    }

    setUploadedFiles((state) => state.map(
      (uploadedFile) => (fileId === uploadedFile.id ? { ...uploadedFile, ...data } : uploadedFile),
    ));
  };

  const processUpload = (uploadFile, url, isVideo = false) => {
    const data = new FormData();
    data.append('file', uploadFile.file, uploadFile.name);

    api
      .post(url, data, {
        onUploadProgress: (e) => {
          const progress = parseInt(Math.round((e.loaded * 100) / e.total), 10);
          console.log('progress upload', progress);
          updateFile(uploadFile.id, { progress }, isVideo);
        },
      })
      .then((response) => {
        const { data: fileData } = response;
        updateFile(uploadFile.id, {
          uploaded: true,
          id: fileData._id,
          name: fileData.name,
          url: fileData.image_url ? fileData.image_url : fileData.video_url,
        }, isVideo);
      })
      .catch(() => {
        updateFile(uploadFile.id, {
          error: true,
        }, isVideo);
      });
  };

  const handleUpload = (files = []) => {
    const newFiles = files.map((file) => ({
      file,
      id: uniqueId(),
      name: file.name,
      readableSize: filesize(file.size),
      preview: URL.createObjectURL(file),
      progress: 0,
      uploaded: false,
      error: false,
      url: null,
    }));

    setUploadedFiles([...newFiles, ...uploadedFiles]);

    newFiles.forEach((file) => processUpload(file, `/images/${animalId}/animals`));
  };

  const handleUploadVideo = (files = []) => {
    if (uploadedFileVideo) {
      return;
    }

    const file = files[0];
    const newFile = {
      file,
      id: uniqueId(),
      name: file.name,
      readableSize: filesize(file.size),
      // preview: URL.createObjectURL(file),
      progress: 0,
      uploaded: false,
      error: false,
      url: null,
    };

    setUploadedFileVideo(newFile);

    processUpload(newFile, `/videos/${animalId}/animals`, true);
  };

  useEffect(() => {
    (async () => {
      try {
        setShowButton(true);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  const handlePrev = () => {
    navigate('/anuncio-detalhes');
  };

  const handleFinish = () => {
    localStorage.removeItem(ANUNCIO_DATA);
    navigate('/anuncios');
  };

  useEffect(() => {
    const data = JSON.parse(localStorage.getItem(ANUNCIO_DATA));
    if (data) {
      console.log(data);
    }
  }, []);

  return (
    <Container>
      <Row>
        <Col>
          <Card>
            <CardHeader>
              <h3>Adicione Fotos e VÃ­deo</h3>
            </CardHeader>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Card>
            <CardHeader>
              <h5>Imagens</h5>
            </CardHeader>
            <CardBody>
              <Container>
                <Upload onUpload={handleUpload} />
                <FileList files={uploadedFiles} />
                {/* <FileList
                  files={[
                    {
                      id: 10,
                      preview:
                        'https://s2.glbimg.com/V4XsshzNU57Brn3e127b80Rbk24=/e.glbimg.com/og/ed/f/original/2016/05/30/gado.jpg',
                      url: 'https://s2.glbimg.com/V4XsshzNU57Brn3e127b80Rbk24=/e.glbimg.com/og/ed/f/original/2016/05/30/gado.jpg',
                      name: 'animal bovino.jpg',
                      progress: 33,
                    },
                  ]}
                /> */}
              </Container>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Card>
            <CardHeader>
              <h5>VÃ­deo</h5>
              <small>Envie apenas um vÃ­deo</small>
            </CardHeader>
            <CardBody>
              <Container>
                {!uploadedFileVideo && (
                  <Upload
                    accept="video/*"
                    message="Selecione ou Arraste o arquivo aqui..."
                    messageAccept="Solte o arquivo aqui"
                    onUpload={handleUploadVideo}
                  />
                )}
                <FileList files={[uploadedFileVideo]} />

                {/* <FileList
                  files={[
                    {
                      id: 11,
                      name: 'animal bovino.mp4',
                      progress: 33,
                    },
                  ]}
                /> */}
              </Container>
            </CardBody>
          </Card>
        </Col>
      </Row>
      {showButton && (
        <Row>
          <Col>
            <Card>
              <CardFooter>
                <Row className="justify-content-between px-3">
                  <Button onClick={handlePrev}>Anterior</Button>
                  <Button onClick={handleFinish}>Finalizar</Button>
                </Row>
              </CardFooter>
            </Card>
          </Col>
        </Row>
      )}
    </Container>
  );
}

export default AnuncioVideoPhotos;
