import React from 'react';
import { Row, Progress, Container, Col } from 'reactstrap';

const FileList = ({ files }) => (
  <Container className="py-2">
    {files.map(
      (uploadedFile) => uploadedFile && (
      <Row className="align-items-center" key={uploadedFile.id}>
        {uploadedFile.preview && (
        <Col sm={1}>
          <div
            style={{
              width: '36px',
              height: '36px',
              borderRadius: '5px',
              backgroundImage: `url(${uploadedFile.preview})`,
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'cover',
              backgroundPosition: '50% 50%',
              marginRight: '10px',
            }}
          />
        </Col>
        )}
        <Col>
          <Row key={uploadedFile.id}>
            <div>
              <strong className="mr-2">{uploadedFile.name}</strong>
              {uploadedFile.url && (
              <a
                href={uploadedFile.url}
                target="_blank"
                rel="noopener noreferrer"
              >
                <i className="fas fa-link" />
              </a>
              )}
              {/* <span>
                  {uploadedFile.readableSize}
                  {' '}
                  {!!uploadedFile.url && (
                    <Button color="danger" size="sm" onClick={() => onDelete(uploadedFile.id)}>
                      Excluir
                    </Button>
                  )}
                </span> */}
            </div>
          </Row>
          <Row>
            {uploadedFile.error && (
            <small className="text-danger">
              <i className="fas fa-exclamation-circle mr-1" />
              Erro no upload. Tente enviar novamente.
            </small>
            )}

            {!uploadedFile.error && !uploadedFile.uploaded && (
            <div className="w-100">
              <small className="text-info mr-3">
                <i className="fas fa-file-upload mr-1" />
                Fazendo upload do arquivo
              </small>
              {uploadedFile.progress ? (
                <Progress
                  color="info"
                  value={uploadedFile.progress}
                  style={{ height: '0.5rem' }}
                />
              ) : null}
            </div>
            )}

            {uploadedFile.uploaded && (
              <small className="text-success">
                <i className="fas fa-check-circle mr-1" />
                Upload concluido com sucesso
              </small>
            )}
          </Row>
        </Col>
      </Row>
      ),
    )}
  </Container>
);

export default FileList;
