/* eslint-disable no-nested-ternary */
import React from 'react';
import Dropzone from 'react-dropzone';

export default function Upload({
  accept = 'image/*',
  message = 'Selecione ou Arraste os arquivos aqui...',
  messageReject = 'Arquivo nÃ£o suportado',
  messageAccept = 'Solte os arquivos aqui',
  onUpload,
}) {
  const renderDragMessage = (isDragActive, isDragReject) => {
    if (!isDragActive) {
      return (
        <p
          className="d-flex justify-content-center align-items-center py-3"
          style={{ color: 'var(--dark)' }}
        >
          {message}
        </p>
      );
    }

    if (isDragReject) {
      return (
        <p
          className="d-flex justify-content-center align-items-center py-3"
          style={{ color: 'var(--danger)' }}
        >
          {messageReject}
        </p>
      );
    }

    return (
      <p
        className="d-flex justify-content-center align-items-center py-3"
        style={{ color: 'var(--success)' }}
      >
        {messageAccept}
      </p>
    );
  };

  return (
    <Dropzone accept={accept} onDropAccepted={onUpload}>
      {({ getRootProps, getInputProps, isDragActive, isDragReject }) => (
        <div
          {...getRootProps()}
          className="dropzone w-100"
          style={{
            border: `1px dashed ${
              isDragActive ? 'var(--success)'
                : isDragReject ? 'var(--danger)' : ''
            }`,
            borderRadius: '0.75rem',
            cursor: 'pointer',
            transition: 'height 0.2s ease',
          }}
        >
          <input {...getInputProps()} />
          {renderDragMessage(isDragActive, isDragReject)}
        </div>
      )}
    </Dropzone>
  );
}
